* {
  margin: 0;
  padding: 0;
}

.container {
  h1 {
    color: red;
  }
}

$size: 100px;
$border: 1px solid black;
$color: orange;

//전역변수로 선언
@mixin color {
  color: red;
  background-color: yellow;
  @content;
}
.box {
  .event {
    width: 320px;
    height: $size;
    background: #ccc;
    border: $border;
    margin: {
      top: 10px;
      left: 10px;
    } //리팩토링
    //margin-top: 10px;
    //margin-left: 10px;
    //  font-size: 20px;
    // font-weight: bold;
    font: {
      size: 20px;
      font-weight: bold;
    }
    text-align: center;
    line-height: $size;
  }
  & .event01 {
    display: flex;
    font-size: 16px;
    font-weight: 400;
    .eve {
      @include color {
        text-decoration: underline;
        //속성 더 추가하고 싶을때 복잡해지기 때문에 별도속성 뽑아내기
      }
      width: $size;
      text-align: center;
      border: $border;
    }
    & :nth-child(2) {
      margin: 0 10px;
    }
  }
  .event02 {
    @include color();
  }
  .event03 {
    background-color: $color;
    &:hover {
      background-color: darken($color, 30%);
      //sass 에서 사용하는 컬러함수 lighten dark rgba
      // mixxing saturate invert...
    }
  }
}

ul {
  margin-top: 10px;
  li {
    width: $size;
    height: $size;
    list-style: none;
    background-size: cover;
  }
}
